<template>
  <div v-if="login">
    <nav>
      <router-link to="/account">Account</router-link> |
      <router-link to="/login">Login</router-link> |
      <router-link to="/logout">Logout</router-link> |
      <router-link to="/registration">Registration</router-link> |
      <router-link to="/recover-password">Recover-password</router-link> |
      <router-link to="/settings">Settings</router-link> |
      <router-link to="/404">404</router-link> |
      <router-link to="/answers">Answers</router-link> |
      <router-link to="/assign">Assign</router-link> |
      <router-link to="/choice">Choice</router-link> |
      <router-link to="/criteria">Criteria</router-link> |
      <router-link to="/result">Result</router-link>
    </nav>
    <router-view />
  </div>
  <div style="max-width: 80%; margin:200px auto;" v-if="!login">
    <input class="uui-form_input-6 w-input" maxlength="256"  v-model="myText"
      placeholder="passvord" type="text">
      <div style="color: red;margin: 5px;">{{Error}}</div>
      <input @click="check" type="submit" class="uui-button-7 w-button" value="Начать">
  </div>
</template>

<style src="./assets/normalize.css"></style>
<style src="./assets/webflow.css"></style>
<style src="./assets/analitichka.webflow.css"></style>

<script>
export default {
  data() {
    return {
      login: false,
      passvord: "Sz97HMU2lArV",
      myText: '',
      Error: '',
      token: ''
    }
  },
  methods: {
    check() {
      if(this.passvord == this.myText) {
        this.login = !this.login
      } else {
        this.Error = 'доступ закрыт'
      }
    },

  async CreateSession() {
    try {
      const response = await fetch(this.$store.state.ApiLink + "/api/v1/auth/create_session", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      });

      const data = await response.json();
      document.cookie = `session_token=${data.session_token}; path=/; Secure`;
    } catch (error) {
      console.error("Fetch error:", error);
    }
  },
  async Refresh_Session() {
    try {
      const response = await fetch(this.$store.state.ApiLink + "/api/v1/auth/refresh_access", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      });

      const data = await response.json();
      document.cookie = `session_token=${data.session_token}; path=/; Secure`;
    } catch (error) {
      console.error("Fetch error:", error);
    }
  },
  async UserInfo() {
      try {
        const response = await fetch(this.$store.state.ApiLink + '/api/v1/user/user_info', {
          method: 'GET',
          headers: {
            "Content-Type": "application/json"
          },
          credentials: 'include', // Отправка куков
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const data = await response.json();
        console.log(data.email);
        this.$store.state.UserMail = data.email
      } catch (error) {
        this.error = error.message;
        console.error('Ошибка при выполнении запроса:', error);
        this.$myMethod('/login')
      }
    }
  },
  beforeMount() {
    this.UserInfo()
  }
}
</script>
