<script>
import HeaderVue from '@/components/HeaderVue.vue';
import QuestionVue from '@/components/QuestionVue.vue';
import CarouselVue from '@/components/CarouselVue.vue';
import FooterVue from '@/components/FooterVue.vue';

export default {
  name: 'LoginView',
  components: {
    HeaderVue,
    QuestionVue,
    CarouselVue,
    FooterVue
  },
  data() {
    return {
      email: '',
      password: '',
      serverError: {},
      showText: false,
      token: ''
    }
  },
  methods: {
    async UserInfo() {
      try {
        const response = await fetch(this.$store.state.ApiLink + '/api/v1/user/user_info', {
          method: 'GET',
          headers: {
            "Content-Type": "application/json"
          },
          credentials: 'include', // Отправка куков
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const data = await response.json();
        console.log(data.email);
        this.$store.state.UserMail = data.email
      } catch (error) {
        this.error = error.message;
        console.error('Ошибка при выполнении запроса:', error);
        this.$myMethod('/login')
      }
    },
    async Login() {
  const newUser = {
    email: this.email,
    password: this.password,
  };

  if (this.password.length >= 8) {
    try {
      const response = await fetch(this.$store.state.ApiLink + "/api/v1/auth/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newUser),
      });

      if (response.ok) {
        const login = await response.json();

        // Сохраняем токен в cookie (без HttpOnly, чтобы JS мог его читать)
        document.cookie = `access_token=${login.access_token}; path=/; Secure`;

        // Читаем токен из cookie и записываем в this.token
        this.token = this.getCookie("access_token");

        // Выводим токен для проверки
        console.log("Сохранённый токен:", this.token);
        this.UserInfo()
        this.$myMethod('/'); // Перенаправление при успешном входе
      } else {
        this.serverError = await response.json();
      }
    } catch (error) {
      console.error("Fetch error:", error);
    }
  } else {
    this.serverError = { detail: 'Пароль должен содержать минимум 8 символов.' };
  }
},

// Метод для получения значения куки по имени
getCookie(name) {
  const matches = document.cookie.match(new RegExp(
    "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
  ));
  return matches ? decodeURIComponent(matches[1]) : undefined;
}


  }
}
</script>
