<script>
import HeaderVue from '@/components/HeaderVue.vue';
import QuestionVue from '@/components/QuestionVue.vue';
import CarouselVue from '@/components/CarouselVue.vue';
import FooterVue from '@/components/FooterVue.vue';

export default {
  name: 'AccountView',
  components: {
    HeaderVue,
    QuestionVue,
    CarouselVue,
    FooterVue
  },
  data() {
    return {
      filter: [
        {
        title: 'Все вопросы',
        active: true
      },
      {
        title: 'Решено',
        active: false
      },
      {
        title: 'Продолжить',
        active: false
      },
      {
        title: 'Рекомендуем',
        active: false
      },
    ],
      question: [
        {
          status: 'Решено',
          question: 'Какой мой любимый фильм?',
          description: 'Надо взвесить всё: цену, расход топлива, безопасность, комфорт, дизайн.',
          title: 'User Userovich',
          date: '11 Янв 2024',
          time:'около 52 мин.',
          img: '../images/geo-shape-09.svg'
        },
        {
          status: 'Решено',
          question: 'Какой автомобиль мне купить?',
          description: 'Linear helps streamline software projects, sprints, tasks, and bug tracking. Here’s how to get started.',
          title: 'User Userovich',
          date: '11 Дек 2022',
          time:'около 12 мин.',
          img: '../images/geo-shape-11.svg'
        },
        {
          status: 'Продолжить',
          question: 'Выбрать лучшего сотрудника в офисе за последние пол года',
          description: 'Сравнить их по опыту, навыкам, образованию и рекомендациям. Возможно, опыт перевесит, но лучше учесть все факторы, чтобы не ошибиться.',
          title: 'User Userovich',
          date: '11 Jan 2022',
          time:'около 51 мин.',
          img: '../images/geo-shape-04.svg'
        },
        {
          status: 'Рекомендуем',
          question: 'Выбор места для отдыха',
          description: 'Решение: климат, цена, безопасность, жильё, сколько всего интересного можно увидеть. Мест много, но кажется, климат и безопасность сейчас главные.',
          title: 'Admin Rual',
          date: '11 Jan 2022',
          time:'около 52 мин.',
          img: '../images/geo-shape-05.svg'
        },
        {
          status: 'Рекомендуем',
          question: 'Какой смартфон купить?',
          description: 'Нужно сравнить камеры, производительность, автономность, цену и бренд',
          title: 'Admin Fufa',
          date: '11 Jan 2022',
          time:'около 52 мин.',
          img: '../images/geo-shape-01.svg'
        },
      ]
    }
  },
  computed: {
    avatarStyle() {
      const colors = this.$store.state.colors;
      const userMail = this.$store.state.UserMail[0] || '';
      const index = this.hashString(userMail) % colors.length;
      return { backgroundColor: colors[index] };
    },
    
  },
  methods: {
    hashString(str) {
      let hash = 0;
      for (let i = 0; i < str.length; i++) {
        hash = str.charCodeAt(i) + ((hash << 5) - hash);
      }
      return Math.abs(hash);
    },
    setActive(selectedIndex) {
    this.filter = this.filter.map((item, index) => ({
      ...item,
      active: index === selectedIndex
    }));
  },
  getFilteredQuestions() {
    // Находим активный фильтр
    const activeFilter = this.filter.find(f => f.active)?.title;

    return this.question.filter(question => {
      if (activeFilter === 'Все вопросы') {
        return true; // Показываем все вопросы
      }
      return question.status === activeFilter;
    });
  }
  }
}
</script>
