import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AccountView from '../views/AccountView.vue'
import LoginView from '../views/LoginView.vue'
import LogoutView from '../views/LogoutView.vue'
import RegistrationView from '../views/RegistrationView.vue'
import RecoverPasswordView from '../views/RecoverPasswordView.vue'
import SettingsView from '../views/SettingsView.vue'
import ForView from '../views/404View.vue'
import AnswersView from '../views/AnswersView.vue'
import AssignView  from '../views/AssignView.vue'
import ChoiceView  from '../views/ChoiceView.vue'
import CriteriaView  from '../views/CriteriaView.vue'
import ResultView  from '../views/ResultView.vue'
const router = createRouter({
  history: createWebHashHistory(),
  routes: [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/account',
    name: 'account',
    component:  AccountView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/logout',
    name: 'logout',
    component: LogoutView
  },
  {
    path: '/registration',
    name: 'registration',
    component: RegistrationView
  },
  {
    path: '/recover-password',
    name: 'recover-password',
    component: RecoverPasswordView
  },
  {
    path: '/settings',
    name: 'settings',
    component: SettingsView
  },
  {
    path: '/404',
    name: '404',
    component: ForView
  },
  {
    path: '/answers',
    name: 'answers',
    component: AnswersView 
  },
  {
    path: '/assign',
    name: 'assign',
    component: AssignView 
  },
  {
    path: '/choice',
    name: 'choice',
    component: ChoiceView 
  },
  {
    path: '/criteria',
    name: 'criteria',
    component: CriteriaView  
  },
  {
    path: '/result',
    name: 'result',
    component: ResultView  
  },
]
})

export default router
