import FooterVue from '@/components/FooterVue.vue';

export default {
  name: 'RegistrationView',
  components: {
    HeaderVue,
    QuestionVue,
    CarouselVue,
    FooterVue
  },
  data() {
    return {
      name: '',
      email: '',
      password: '',
      showText: false,
      serverError: ''
    }
  },
  methods: {
    async Regisration() {
      const newUser = {
        name: this.name,
        email: this.email,
        password: this.password,
      };

      if (this.name !== '' && this.email !== '' && this.password.length >= 8 && !/\d/.test(this.name) && !/\s/.test(this.password)) {
        try {
          const response = await fetch(this.$store.state.ApiLink + "/api/v1/auth/register", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(newUser),
          });

          if (response.ok) {
            this.$myMethod('/login'); // Перенаправление при успешной регистрации
          } else {
            this.serverError = await response.json(); // Ждём JSON-ответ
          }
        } catch (error) {
          console.error("Fetch error:", error);
        }
      }
    }
  }
}
</script>
