<template>
  <section class="uui-section_faq01">
    <div class="uui-page-padding">
      <div class="uui-container-large">
        <div class="uui-padding-vertical-xhuge">
          <div class="uui-text-align-center">
            <div class="uui-max-width-large align-center">
              <div class="uui-heading-subheading">Вопросы</div>
              <h2 class="uui-heading-medium">Для чего это нужно?</h2>
              <div class="uui-space-xsmall"></div>
              <div class="uui-text-size-large">Как правильно пользоваться сервисом и зачем он нужен? </div>
            </div>
          </div>
          <div class="uui-faq01_component">
            <div class="uui-faq01_list">
              <div class="uui-faq01_accordion" v-for="(q, index) in questions" :key="index">
                <div class="uui-faq01_question" @click="toggle(index)">
                  <div class="uui-faq01_heading">{{ q.title }}</div>
                  <div class="uui-faq01_icon-wrapper">
                    <div class="accordion-icon_component" :class="{ open: activeIndex === index }">
                      <div class="accordion-icon_horizontal-line"></div>
                      <div class="accordion-icon_vertical-line"></div>
                    </div>
                  </div>
                </div>
                <transition name="accordion">
                  <div v-if="activeIndex === index" class="uui-faq01_answer">
                    <div class="uui-text-size-medium">{{ q.text }}</div>
                    <div class="uui-space-medium"></div>
                  </div>
                </transition>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: null,
      questions: this.$store.state.questions
    };
  },
  methods: {
    toggle(index) {
      this.activeIndex = this.activeIndex === index ? null : index;
    }
  }
};
</script>

<style scoped>
.uui-faq01_accordion:last-child {
  border: none;
}

.accordion-icon_component {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  position: relative;
}

.accordion-icon_horizontal-line, .accordion-icon_vertical-line {
  position: absolute;
  background-color: #7f56d9;
  transition: transform 0.3s ease;
}

.accordion-icon_horizontal-line {
  width: calc(100% - 4px);
  height: 2px;
}

.accordion-icon_vertical-line {
  width: 2px;
  height: calc(100% - 4px);
}

.accordion-icon_component.open .accordion-icon_vertical-line {
  transform: rotate(90deg);
}

.accordion-enter-active, .accordion-leave-active {
  transition: all 0.3s ease;
  max-height: 200px;
  opacity: 1;
}

.accordion-enter-from, .accordion-leave-to {
  max-height: 0;
  opacity: 0;
}
</style>
